import styled from "styled-components";
import SystemUtil from "../../utils/systemUtil";
import SetterFrame from "./setterFrame";
import { useMemo, useState } from "react";
import assert from "assert";
import Sketch from "react-p5";
import p5Types from "p5";

namespace EditDialog {

    type LocalSatate = {
        startX: number;
        startY: number;
        circle: CircleInfo | null;
    }
    type Mode = 'none' | 'rad' | 'num' | 'dir'
    type CircleInfo = {
        dir: number;
        divNum: number;
        radX: number;
        radY: number;
        mode: Mode;
    }

    const CANVAS_WIDTH = SystemUtil.CONTENTS_WIDTH - 16;

    export const Component = (props: {
        ls: SetterFrame.LocalState;
        invalidate: () => void;
    }) => {
        const ps = props.ls;
        const curUnit = ps.units[ps.focusUnit];
        const curSeed = curUnit.seeds[ps.focusSeed];
        const curLayer = curSeed.layers[ps.focusLayer];

        // const invalidate = props.invalidate;

        const [ls, setLs] = useState<LocalSatate>({
            startX: 0,
            startY: 0,
            circle: null
        });

        const invalidate = () => setLs({ ...ls });


        useMemo(() => {
            ls.circle = curLayer.method !== 'circle' ? null : {
                radX: 100,
                radY: 100,
                dir: 0,
                divNum: 4,
                mode: 'none'
            }
        }, [curLayer.method]);

        const deligatePoint = (clientRect: DOMRect, clientX: number, clientY: number, callback: (x: number, y: number) => void) => {
            const WIDTH = SystemUtil.CONTENTS_WIDTH - 16;
            const rate = WIDTH / clientRect.width;
            // console.log(`RATE:${rate}`);
            const x = (-clientRect.left + clientX) * rate;
            const y = (-clientRect.top + clientY) * rate;
            const subX = -WIDTH / 2 + x;
            const subY = -WIDTH / 2 + y;
            callback(subX, subY)
        }
        const drawPoint = (clientRect: DOMRect, clientX: number, clientY: number) => {
            deligatePoint(clientRect, clientX, clientY, (x: number, y: number) => {
                const pars = curLayer.pars;
                pars.push({ x, y });
                invalidate();
            });
        }

        const setStartPoint = (clientRect: DOMRect, clientX: number, clientY: number) => {
            deligatePoint(clientRect, clientX, clientY, (x: number, y: number) => {
                ls.startX = x;
                ls.startY = y;
                assert(ls.circle != null, 'ls.circlenull');
                const cir = ls.circle;
                ls.startX = x;
                ls.startY = y;
                if (Math.abs(cir.radX - x) <= 10 && Math.abs(cir.radY - y) <= 10) {
                    cir.mode = 'rad';
                } else {
                    cir.mode = 'num';
                }
                invalidate();
            });
        }

        const release = (clientRect: DOMRect, clientX: number, clientY: number) => {
            deligatePoint(clientRect, clientX, clientY, (x: number, y: number) => {
                ls.startX = x;
                ls.startY = y;
                assert(ls.circle != null, 'ls.circlenull');
                const cir = ls.circle;
                console.log('release');
                cir.mode = 'none';
                invalidate();
            });
        }

        const calcCircle = (clientRect: DOMRect, clientX: number, clientY: number) => {
            deligatePoint(clientRect, clientX, clientY, (x: number, y: number) => {
                assert(ls.circle != null, 'ls.circlenull');
                const cir = ls.circle;
                if (cir.mode === 'rad') {
                    ls.circle.radX += (x - ls.startX);
                    ls.circle.radY += (y - ls.startY);
                    ls.startX = x;
                    ls.startY = y;
                } else if (cir.mode === 'num') {
                    ls.circle.divNum = Math.floor(Math.abs(ls.startX - x) * 0.2);
                    ls.circle.dir = ls.startY - y * 0.01;
                    if (ls.circle.divNum <= 2) ls.circle.divNum = 2;
                }

                // console.log('update');
                curLayer.pars.length = 0;
                for (let i = 0; i < ls.circle.divNum; i++) {
                    const rad = i * Math.PI * 2 / ls.circle.divNum + ls.circle.dir;
                    const x = Math.cos(rad) * ls.circle.radX;
                    const y = Math.sin(rad) * ls.circle.radY;
                    curLayer.pars.push({ x, y });
                }
                invalidate();
            });
        }

        return (
            <_Wnd
                onMouseDown={(e) => {
                    const rect = e.currentTarget.getClientRects()[0];
                    switch (curLayer.method) {
                        case 'draw': {
                            drawPoint(rect, e.clientX, e.clientY);
                        } break;
                        case 'circle': {
                            setStartPoint(rect, e.clientX, e.clientY);
                        } break;
                    }
                }}
                onMouseMove={(e) => {
                    if (!e.buttons) return;
                    const rect = e.currentTarget.getClientRects()[0];
                    switch (curLayer.method) {
                        case 'draw': {
                            drawPoint(rect, e.clientX, e.clientY);
                        } break;
                        case 'circle': {
                            calcCircle(rect, e.clientX, e.clientY);
                        } break;
                    }
                }}
                onMouseUp={(e) => {
                    const rect = e.currentTarget.getClientRects()[0];
                    switch (curLayer.method) {
                        case 'draw': {
                        } break;
                        case 'circle': {
                            release(rect, e.clientX, e.clientY);
                        } break;
                    }
                }}
                onTouchStart={(e) => {
                    const rect = e.currentTarget.getClientRects()[0];
                    const t = e.touches[0];
                    switch (curLayer.method) {
                        case 'draw': {
                        } break;
                        case 'circle': {
                            setStartPoint(rect, t.clientX, t.clientY);
                        } break;
                    }
                }}
                onTouchMove={(e) => {
                    const rect = e.currentTarget.getClientRects()[0];
                    const t = e.touches[0];
                    switch (curLayer.method) {
                        case 'draw': {
                            drawPoint(rect, t.clientX, t.clientY);
                        } break;
                        case 'circle': {
                            calcCircle(rect, t.clientX, t.clientY);
                        } break;
                    }
                }}
                onTouchCancel={(e) => {
                    const rect = e.currentTarget.getClientRects()[0];
                    const t = e.touches[0];
                    switch (curLayer.method) {
                        case 'draw': {
                        } break;
                        case 'circle': {
                            release(rect, t.clientX, t.clientY);
                        } break;
                    }
                }}
            >
                <Sketch setup={(p5: p5Types, canvasParentRef: Element) => {
                    p5.createCanvas(CANVAS_WIDTH, CANVAS_WIDTH).parent(canvasParentRef);
                    p5.background('#0b0336');
                    // p5.filter(p5.BLUR, 4);
                    p5.frameRate(60);
                }} draw={(p5: p5Types) => {
                    p5.background('#0b0336');
                    p5.noStroke();

                    const drawLayer = (layer: SetterFrame.BarrLayer) => {
                        layer.pars.forEach((par) => {
                            const x = CANVAS_WIDTH / 2 + par.x;
                            const y = CANVAS_WIDTH / 2 + par.y;

                            p5.fill(layer.barrColor[0]);
                            p5.ellipse(x, y, 10, 10);
                        });
                    }
                    curSeed.layers.forEach((layer, i) => {
                        if (ps.focusLayer === i) return -1;
                        drawLayer(layer);
                    });
                    p5.background('#0b033690');

                    p5.fill('#fff');
                    p5.rect(CANVAS_WIDTH / 2 - 10, CANVAS_WIDTH / 2 - 10, 20, 20);

                    if (ls.circle != null) {
                        const cir = ls.circle;
                        switch (cir.mode) {
                            case 'none': { p5.fill('#99f0ff1c'); } break;
                            case 'rad': { p5.fill('#ffffff65'); } break;
                            case 'num': { p5.fill('#fcff5d6a'); } break;
                            case 'none': { p5.fill('#99f0ff5e'); } break;
                        }
                        p5.rect(CANVAS_WIDTH / 2 - cir.radX, CANVAS_WIDTH / 2 - cir.radY, cir.radX * 2, cir.radY * 2);

                        p5.fill('#eeff00');
                        p5.rect(CANVAS_WIDTH / 2 + cir.radX - 10, CANVAS_WIDTH / 2 + cir.radY - 10, 20, 20);
                    }
                    drawLayer(curLayer);
                }} />
            </_Wnd>
        );
    }
}

export default EditDialog;



const _Wnd = styled.div`
    display: inline-block;
    position: relative;
    
    background-color: #000000d2;
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    margin: 8px 0 0 8px;
`;